import Axios, { AxiosInstance, AxiosRequestConfig } from 'axios';
import { PureHttpError, PureHttpResoponse, PureHttpRequestConfig, Request, Response as res } from './types';
import md5 from 'md5';
import qs from 'qs';
import { loadEnv } from '@/build';

// Âä†ËΩΩÁéØÂ¢ÉÂèòÈáè VITE_PROXY_DOMAINÔºàÂºÄÂèëÁéØÂ¢ÉÔºâ  VITE_PROXY_DOMAIN_REALÔºàÊâìÂåÖÂêéÁöÑÁ∫ø‰∏äÁéØÂ¢ÉÔºâ
const { VITE_PROXY_DOMAIN, VITE_PROXY_DOMAIN_REAL } = loadEnv();

// Áõ∏ÂÖ≥ÈÖçÁΩÆËØ∑ÂèÇËÄÉÔºöwww.axios-js.com/zh-cn/docs/#axios-request-config-1
const defaultConfig: AxiosRequestConfig = {
  baseURL: process.env.NODE_ENV === 'production' ? VITE_PROXY_DOMAIN_REAL : VITE_PROXY_DOMAIN,
  // ÂΩìÂâç‰ΩøÁî®mockÊ®°ÊãüËØ∑Ê±ÇÔºåÂ∞ÜbaseURLÂà∂Á©∫ÔºåÂ¶ÇÊûú‰Ω†ÁöÑÁéØÂ¢ÉÁî®Âà∞‰∫ÜhttpËØ∑Ê±ÇÔºåËØ∑Âà†Èô§‰∏ãÈù¢ÁöÑbaseURLÂêØÁî®‰∏äÈù¢ÁöÑbaseURLÔºåÂπ∂Â∞Ü11Ë°å„ÄÅ16Ë°å‰ª£Á†ÅÊ≥®ÈáäÂèñÊ∂à
  timeout: 10000,
  headers: {
    Accept: 'application/json, text/plain, */*',
    'Content-Type': 'application/json',
    'X-Requested-With': 'XMLHttpRequest'
  },
  // Êï∞ÁªÑÊ†ºÂºèÂèÇÊï∞Â∫èÂàóÂåñ
  paramsSerializer: (params) => qs.stringify(params, { indices: false }),
  transformRequest: [
    (data, config) => {
      // @ts-ignore
      switch (config['Content-Type'].toLowerCase()) {
        case 'application/x-www-form-urlencoded': {
          return qs.stringify(data);
        }
        case 'multipart/form-data;charset=utf-8': {
          return data;
        }
        default: {
          return JSON.stringify(data);
        }
      }
    }
  ]
};

class PureHttp {
  private static caches: any = {};
  constructor() {
    this.httpInterceptorsRequest();
    this.httpInterceptorsResponse();
  }
  // ÂàùÂßãÂåñÈÖçÁΩÆÂØπË±°
  private static initConfig: PureHttpRequestConfig = {};

  // ‰øùÂ≠òÂΩìÂâçAxiosÂÆû‰æãÂØπË±°
  private static axiosInstance: AxiosInstance = Axios.create(defaultConfig);

  // ËØ∑Ê±ÇÊã¶Êà™
  private httpInterceptorsRequest(): void {
    PureHttp.axiosInstance.interceptors.request.use(
      (config: PureHttpRequestConfig) => {
        const $config = config;
        // ‰ºòÂÖàÂà§Êñ≠post/getÁ≠âÊñπÊ≥ïÊòØÂê¶‰º†ÂÖ•ÂõûÊéâÔºåÂê¶ÂàôÊâßË°åÂàùÂßãÂåñËÆæÁΩÆÁ≠âÂõûÊéâ
        if (typeof config.beforeRequestCallback === 'function') {
          config.beforeRequestCallback($config);
          return $config;
        }
        if (PureHttp.initConfig.beforeRequestCallback) {
          PureHttp.initConfig.beforeRequestCallback($config);
          return $config;
        }
        const token = sessionStorage.getItem('token');
        if (token) {
          // @ts-ignore
          config.headers['Authorization'] = 'Bearer ' + token;
          return $config;
        }
        return $config;
      },
      (error) => {
        return Promise.reject(error);
      }
    );
  }

  // ÂìçÂ∫îÊã¶Êà™
  private httpInterceptorsResponse(): void {
    const instance = PureHttp.axiosInstance;
    instance.interceptors.response.use(
      (response: PureHttpResoponse) => {
        const $config = response.config;

        // ‰ºòÂÖàÂà§Êñ≠post/getÁ≠âÊñπÊ≥ïÊòØÂê¶‰º†ÂÖ•ÂõûÊéâÔºåÂê¶ÂàôÊâßË°åÂàùÂßãÂåñËÆæÁΩÆÁ≠âÂõûÊéâ
        if (typeof $config.beforeResponseCallback === 'function') {
          $config.beforeResponseCallback(response);
          return response.data;
        }
        if (PureHttp.initConfig.beforeResponseCallback) {
          PureHttp.initConfig.beforeResponseCallback(response);
          return response.data;
        }
        return response.data;
      },
      (error: PureHttpError) => {
        const $error = error;
        // ÊâÄÊúâÁöÑÂìçÂ∫îÂºÇÂ∏∏ Âå∫ÂàÜÊù•Ê∫ê‰∏∫ÂèñÊ∂àËØ∑Ê±Ç/ÈùûÂèñÊ∂àËØ∑Ê±Ç
        $error.isCancelRequest = Axios.isCancel($error);
        return Promise.reject($error.response?.data);
      }
    );
  }

  // ÈÄöÁî®ËØ∑Ê±ÇÂ∑•ÂÖ∑ÂáΩÊï∞
  public request<T>({
    method = 'GET',
    url,
    data = {},
    cache = false,
    headers = {}
  }: Request): Promise<T> {
    let key: string;
    let params: object = {};
    if (cache) {
      key = md5(params ? method + url + JSON.stringify(params) : method + url);
      if (PureHttp.caches[key]) return PureHttp.caches[key];
      PureHttp.caches[key] = null;
    }
    if (process.env.NODE_ENV === 'development') {
      console.log(
        `method: ${method} / url: ${url} / body: ${JSON.stringify(
          params
        )} / headers: ${JSON.stringify(headers)}`
      );
    }
    if (method.toLocaleUpperCase() === 'GET') {
      params = data;
      data = {};
    }
    // ÂçïÁã¨Â§ÑÁêÜËá™ÂÆö‰πâËØ∑Ê±Ç/ÂìçÂ∫îÂõûÊéâ
    return new Promise((resolve, reject) => {
      PureHttp.axiosInstance
        .request({ method, url, params, data, headers })
        .then((response: any) => {
          if (cache) PureHttp.caches[key] = response;
          resolve(response);
        })
        .catch((error) => {
          reject(error);
        });
    });
  }
}

const http = new PureHttp();
export const request = http.request;
export type Response = res
