import { defineStore } from "pinia";
import { defineAsyncComponent, reactive, ref, shallowRef } from "vue";
import { routes } from '@/router';
import Layout from '@/components/layout/index.vue';

export const useMenuStore = defineStore('menu', () => {
  // ÂéüÂßãÂºÇÊ≠•ËèúÂçï
  const originAsyncMenu = reactive<Array<Menu.MenuRow>>([]);
  // Êï¥ÁêÜËøáÁöÑÂºÇÊ≠•ËèúÂçï
  const asyncMenu = reactive<Array<Menu.Route>>([]);
  // ÂΩìÂâçÁî®Êà∑ÁöÑÊâÄÊúâËèúÂçï
  const allMenu = reactive<Array<Menu.Route>>([...routes]);

  // ËÆæÁΩÆÂºÇÊ≠•ËèúÂçï
  const setAsyncMenu = (topMenu: Array<Menu.Route>, subMenu: Array<Menu.Route>) => {
    for (const item of topMenu) {
      mergeMenu(item, subMenu);
    }
    asyncMenu.push(...topMenu, {
      id: 9999,
      path: '/:pathMatch(.*)*',
      component: () => import('@/views/404.vue'),
      meta: {
        title: '404',
        type: 1,
        cache: false,
        pid: 0,
        affix: false
      },
      hidden: true,
      redirect: '/',
    });
    // @ts-ignore
    routes.push(...asyncMenu);
    allMenu.push(...topMenu);
    return asyncMenu;
  };

  // 1.ÂÖàËØÜÂà´ÊâÄÊúâÁöÑviews/Êñá‰ª∂Â§πname/*.vueÊñá‰ª∂
  // ËøôÈáåÈôêÂà∂ÊÄßÂæàÈ´òÔºåÂè™ÊúâË∑ØÂæÑ‰∏∫/views/Êñá‰ª∂Â§πname/*.vueÔºåÁöÑÊñá‰ª∂ÊâçËÉΩËÉåËØÜÂà´ÔºåÂ¶ÇÊûú‰∏çÂú®Ëøô‰∏™ÁªìÊûÑÔºåËá™Â∑±Â¢ûÂä†ÂêßÔºåÁÑ∂ÂêéÂÜçÂêàÂπ∂
  const modules = import.meta.glob('../../views/**/**.vue');
  // ‰∏äÈù¢ÁöÑÁªìÊûúÊòØ‰∏Ä‰∏™ÂØπË±°ÔºåÁõ∏ÂΩì‰∫é
  // const modules ={
  //     "../views/daily/index.vue": () => import("/src/views/daily/index.vue")
  // };

  /**
    Âü∫Êú¨ËØ≠Ê≥ï
    ÊòüÂè∑ ( *) ‚Äî ÂåπÈÖçÈô§ÊñúÊù†ÔºàË∑ØÂæÑÂàÜÈöîÁ¨¶Ôºâ„ÄÅÈöêËóèÊñá‰ª∂Ôºà‰ª• ÂºÄÂ§¥ÁöÑÂêçÁß∞.Ôºâ‰πãÂ§ñÁöÑÊâÄÊúâÂÜÖÂÆπ„ÄÇ
    ÂèåÊòüÊàñ globstar ( **) ‚Äî ÂåπÈÖçÈõ∂‰∏™ÊàñÂ§ö‰∏™ÁõÆÂΩï„ÄÇ
    ÈóÆÂè∑ ( ?) ‚Äì ÂåπÈÖçÈô§ÊñúÊù†ÔºàË∑ØÂæÑÂàÜÈöîÁ¨¶Ôºâ‰ª•Â§ñÁöÑ‰ªª‰ΩïÂçï‰∏™Â≠óÁ¨¶„ÄÇ
    Â∫èÂàó ( [seq]) ‚Äî ÂåπÈÖçÂ∫èÂàó‰∏≠ÁöÑ‰ªª‰ΩïÂ≠óÁ¨¶„ÄÇ
    üìñÂÖ≥‰∫éÂü∫Êú¨ÂåπÈÖçË°å‰∏∫ÁöÑÂá†Âè•ËØù„ÄÇ

    ‰∏Ä‰∫õ‰æãÂ≠êÔºö

    // src/**//*.js  ÂåπÈÖçÁõÆÂΩï‰∏≠ÊâÄÊúâÂÖ∑ÊúâÊâ©Â±ïÂêçÁöÑÊñá‰ª∂srcÔºà‰ªª‰ΩïÁ∫ßÂà´ÁöÑÂµåÂ•óÔºâ „ÄÇ.js
    // src/*.??‚Äî ÂåπÈÖçsrcÁõÆÂΩï‰∏≠ÂÖ∑Êúâ‰∏§‰∏™Â≠óÁ¨¶Êâ©Â±ïÂêçÁöÑÊâÄÊúâÊñá‰ª∂Ôºà‰ªÖÁ¨¨‰∏ÄÁ∫ßÂµåÂ•óÔºâ„ÄÇ
    // file-[01].js‚Äî ÂåπÈÖçÊñá‰ª∂Ôºöfile-0.js, file-1.js.
  **/

  // Â∞ÜÂºÇÊ≠•ËèúÂçïËΩ¨Âåñ‰∏∫Â§çÂêàËßÑÂàôÁöÑË∑ØÁî±
  const resolveMenu = (menu: Menu.MenuRow) => {
    const { id, type, icon, cache, hide, title, url, name, action, sort, path, pid, redirect } = menu;
    return {
      id,
      path: url,
      component: path? modules[`../../views${path}`] : shallowRef(Layout),
      redirect, //ÈáçÂÆöÂêëÂú∞ÂùÄÔºåÂú®Èù¢ÂåÖÂ±ë‰∏≠ÁÇπÂáª‰ºöÈáçÂÆöÂêëÂéªÁöÑÂú∞ÂùÄ
      hidden: hide, // ‰∏çÂú®‰æßËæπÊ†èÊòæÁ§∫
      alwaysShow: true, //‰∏ÄÁõ¥ÊòæÁ§∫Ê†πË∑ØÁî±
      name,
      meta: { type, icon, cache, title, pid, affix: false }, //‰Ω†ÂèØ‰ª•Âú®Ê†πË∑ØÁî±ËÆæÁΩÆÊùÉÈôêÔºåËøôÊ†∑ÂÆÉ‰∏ãÈù¢ÊâÄÊúâÁöÑÂ≠êË∑ØÁî±ÈÉΩÁªßÊâø‰∫ÜËøô‰∏™ÊùÉÈôê
    };
  };

  // Á≠õÈÄâËèúÂçï ÁõÆÂΩï Â§ñÈìæ  ËßÑÂàô
  const filterMenu = (list: Array<Menu.MenuRow>) => {
    const topMenu: Array<Menu.Route> = [];
    const subMenu: Array<Menu.Route> = [];
    const actions: Array<Menu.ActionRow> = [];
    list.sort((a, b) => a.sort - b.sort);
    for (const item of list) {
      const { id, title, action, sort, pid } = item;
      if (item.type === 3) {
        actions.push({
          id, pid, title, action, sort
        });
      } else {
        originAsyncMenu.push(item);
        if (pid === 0) {
          topMenu.push(resolveMenu(item));
        } else {
          subMenu.push(resolveMenu(item));
        }
      }
    }
    return { topMenu, subMenu, actions };
  };

  // ÂêàÂπ∂Áà∂Â≠êË∑ØÁî±
  const mergeMenu = (topDir: Menu.Route, list: Array<Menu.Route>) => {
    const childMenu = [];
    for (const item of list) {
      if (item.meta?.pid === topDir.id) {
        item.path = topDir.path + item.path;
        childMenu.push(item);
      }
    }
    if (childMenu.length) topDir.children = childMenu;
  };

  return {
    filterMenu,
    resolveMenu,
    originAsyncMenu,
    asyncMenu,
    allMenu,
    setAsyncMenu
  };
});
